export default function ({ ratio, count, color, population, year }) {
  const prefix = 're-tooltip';
  const littleMan = Array(10).fill(1).map((_, n) => {
    const fill = (n + 1) < count ? color : '#ccc';
    return `
      <span class="${prefix}-man">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 60 174">
          <ellipse cx="30" cy="17.25" rx="17.07" ry="17.25" style="fill: ${fill}" />
          <path d="M60,53.41a10.44,10.44,0,0,0-10.38-10.5H10.38A10.44,10.44,0,0,0,0,53.41v66.48H15.23V174H44.77V119.88H60Z" style="fill: ${fill}" />
        </svg>
      </span>
    `;
  }).join('');
  return `
  <div class="${prefix}-wrapper">
    <table>
      <tbody>
        <tr class="align-baseline">
          <th rowspan="4" class="${prefix}-title">
            <h3>${year}<small>年</small></h3>
          </th>
          <td class="${prefix}-col-first">
            <span class="${prefix}-label">上網率</span>
          </td>
          <td>
            <span class="${prefix}-value" style="color: ${color}">${ratio}<span class="${prefix}-unit">%</span></span>
          </td>
        </tr>
        <tr>
          <td colspan="2" class="${prefix}-col-first">
            <div class="${prefix}-bar">
              <div class="${prefix}-fill" style="width: ${ratio}%;background: ${color};"></div>
            </div>
          </td>
        </tr>
        <tr class="align-baseline">
          <td class="${prefix}-col-first">
            <span class="${prefix}-label">上網人口</span>
          </td>
          <td>
            <span class="${prefix}-value" style="color: ${color}">${population}<span class="${prefix}-unit">萬人</span></span>
          </td>
        </tr>
        <tr>
          <td colspan="2" class="${prefix}-col-first">
            <div class="${prefix}-population">
              ${littleMan}
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>`
  ;
}



