// import Vue from 'vue';

import template from './template';
import './style.css';

export default class Tooltip {
  constructor(attr) {
    this.attr = attr;
  }

  render(data) {
    Object.assign(this.attr, {
      ...data,
      count: Math.ceil((data.population / this.attr.base) * 10),
    });

    return template(this.attr);
  }
}



