import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/pages/Home';
import Simple from '@/pages/Simple';
import Graphical from '@/pages/Graphical';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/simple',
      name: 'Simple',
      component: Simple,
    },
    {
      path: '/graphical',
      name: 'Graphical',
      component: Graphical,
    },
  ],
});



